import React, { Component } from 'react';

class Home extends Component {
  constructor(props) {
    super(props);
    this.name = '';
  }

  render() {
    return (
      <div className="Background">
            <h1>The game</h1>
            <label>Please enter your name:</label><input onChange={this.props.setName}></input>
            <button onClick={this.props.play}>Continue</button>
      </div>
    );
  }
  
}
export default Home;