import React, { Component } from 'react';
class PlayerStatus extends Component {
    constructor(props) {
      super(props);
    }
  
    render() {
      return (
          <div className="Background">
            <div>{"PLAYERS"}</div>
              {this.props.players.map((player) => <div key={player.id}>{player.name}{player.gameLeader && " * GAMELEADER"}
              {player.finished && ` FINISHED w/ ${player.speed} WPM` }
              </div>)}
            <div>{"SPECTATORS"}</div>
              {this.props.spectators.map((spectator) => <div key={spectator.id}>{spectator.name}</div>)}
        </div>
      );
    }
    
  }
  export default PlayerStatus;