import React, { Component } from 'react';
import PlayerStatus from './PlayerStatus';
import TypeScreen from './TypeScreen';
import io from 'socket.io-client';
import Timer from './Timer';

const connection = "https://e39e09819e6e.ngrok.io";
class Game extends Component {
  constructor(props) {
    super(props);
    this.name = '';
    this.timer = new Timer;
    this.socket = null;
    this.state = {
        gameLeader: false,
        showStart: false,
        showWait: false,
        start: false,
        timer: null,
        time: 3,
        words: 0,
        message: "",
        players: [],
        spectators: [],
        excerpt: ""
    };
    this.startGame = this.startGame.bind(this);
    this.startClick = this.startClick.bind(this);
    this.updateWords = this.updateWords.bind(this);
    this.stopGame = this.stopGame.bind(this);
  }
  listenToMessages() {
    this.socket = io(connection);
    this.socket.emit('join', this.props.name);
    this.socket.on('start', (excerpt) => {
      this.setState({excerpt: excerpt, words: 0, time: 3});
      this.startGame();
    });
    this.socket.on('playerchange', (details) => {
      const detailObj = JSON.parse(details);
      console.log("PLAYER CHANGE", detailObj);
      if (this.socket.id === detailObj.gameLeader) {
        this.setState({gameLeader: true}, () => {
          if (!this.state.start) {
            this.setState({showStart: true});
          }
        });
      }
      this.setState({players: detailObj.players, spectators: detailObj.spectators});
    });
    this.socket.on('gameupdate', (details) => {
      const detailObj = JSON.parse(details);
      this.setState({players: detailObj.players});
    })
    this.socket.on('gamefinished', () => {
      if (this.state.gameLeader) {
        this.setState({showStart: true});
      }
      this.setState({showWait: false});
    });
  }
  
  componentDidMount() {
    this.listenToMessages();
    this.timer.onTick((time) => { this.setState({ time: time})});
  }

  sendUpdates() {
    this.interval = setInterval(() => {
      this.socket.emit('update', {time: this.state.time, words: this.state.words});
    }, 500);
  }
  
  startClick() {
    this.socket.emit('startgame');
    this.setState({showStart: false});
  }

  startGame() {
    this.interval = setInterval(() => {
      this.setState({time: this.state.time - 1});
      if (this.state.time === 0) {
        clearInterval(this.interval);
        this.setState({start: true, showStop: true});
        this.timer.start(); 
        this.sendUpdates();
      }
      }
      , 1000);
  }

  isLeader() {
    let gameLead = false;
    if (this.socket && this.socket.id === this.gameLeader) {
      gameLead = true;
    }
    return gameLead;
  }

  stopGame() {
    this.setState({start: false, showStop: false, showWait: true});
    this.timer.stop();
    clearInterval(this.interval);
    this.socket.emit('playerfinish', {words: this.state.words, time: this.state.time});
    this.timer.reset();
  }

  updateWords(words) {
    this.setState({words: words});  
  }

  render() {
    return (
        <div className="Background">
        <PlayerStatus spectators={this.state.spectators}players={this.state.players} gameLeader={this.state.gameLeader}/>
        {this.state.start ? <TypeScreen update={this.updateWords} finish={this.stopGame} excerpt={this.state.excerpt}/> : "Loading"}
        <div>Time: {this.state.time}</div>
        <div>Correct Words: {this.state.words}</div>
    <div>WPM: {Math.round(60 * this.state.words / this.state.time)}</div>
        {!this.state.start}
        {this.state.showStart && <button onClick={this.startClick}>Start Game</button>}
        {this.state.showWait && <div>Please wait for other players to finish.</div>}
      </div>
    );
  }
  
}
export default Game;